.container{
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies{
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
  padding: 50px;
  width: 80%;
  padding-top: 70px;
}

/* 
부모는 무조건 100%
자식이 부모를 기준으로 80%
(자식px/부모px)*100=%
*/

@media screen and (max-width: 767px) { /* 0~ 767*/
  .movies{
    grid-template-columns: 1fr;
    width: 100%;
  }
}